import { InteracaoNew } from './../../../_shared/model/interacao-new.model';
import { Endpoints } from './../../../_config/endpoints';
import { Interacao } from './../../../_shared/model/interacao.model';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class InteracaoService {

  constructor(private http: HttpClient) { }

  find(id: number) {
    return this.http.get<Interacao[]>(Endpoints.INTERACAO + id);
  }

  insert(interacaoNew: InteracaoNew) {
    return this.http.post<InteracaoNew>(Endpoints.INTERACAO, interacaoNew);
  }
}
